<template lang="pug">
    // 
    #sidebar
        el-menu.el-menu-vertical-demo( default-active="2" @open="handleOpen" @close="handleClose" theme="dark" v-bind:unique-opened= "true"  )
            // 1
            el-submenu(
            v-for="( item, key ) in dataSidebarList"
            v-bind:index="item.titleName"
            v-bind:key="key"
            )
                template( slot="title" )
                    i.material-icons {{ item.iconClass }}
                    span {{ item.titleName }}

                // 2( 3 )
                el-submenu(
                v-for="( itemMiddleSubMenu, keyMiddleSubMenu ) in item.middleSubMenu"
                v-bind:index="itemMiddleSubMenu.titleName"
                v-bind:key="keyMiddleSubMenu"
                )
                    template( slot="title" ) {{ itemMiddleSubMenu.titleName }}
                    el-menu-item(
                    v-for="(itemBottom, keyBottom ) in itemMiddleSubMenu.bottom"
                    v-bind:index="itemBottom.titleName"
                    v-bind:key="keyBottom"
                    @click="clickSidebar( itemBottom.breadcrumb, itemBottom.url )"
                    ) {{ itemBottom.titleName }}

                // 2( 2 )
                el-menu-item-group(
                v-for="( itemMiddleLink, keyMiddleLink ) in item.middleLink"
                v-bind:index="itemMiddleLink.titleName"
                v-bind:key="keyMiddleLink"
                )
                    el-menu-item(
                    v-bind:index="itemMiddleLink.titleName"
                    @click="clickSidebar( itemMiddleLink.breadcrumb, itemMiddleLink.url )"
                    ) {{ itemMiddleLink.titleName }}
</template>

<script>
    import {mapActions, mapGetters} from 'vuex'

    export default {
        data() {
            return {
                dataSidebarList: []
            }
        },
        methods: {
            handleOpen(key, keyPath) {
                // console.log( key, keyPath )
            },
            handleClose(key, keyPath) {
                // console.log( key, keyPath )
            },
            // : url, 
            toPageUrl(linkUrl) {
                location.href = '#/' + linkUrl
            },
            // : (  + url )
            clickSidebar(breadcrumbArr, toUrl) {
                // events 1: 
                let argsLength = breadcrumbArr.length,
                    argumentsArr = []                        // ( : actions )
                for (let i = 0; i < argsLength; i++) {
                    let argumentsObj = new Object
                    argumentsObj['levelName'] = breadcrumbArr[i]
                    argumentsArr.push(argumentsObj)
                }
                // ( argumentsObjactions )
                this.$store.dispatch({
                    type: 'setBreadcrumbLevel',
                    attrObj: argumentsArr
                })
                // events 2: url, 
                this.toPageUrl(toUrl)
            }
        },
        computed: mapGetters({
            get_sideBarList: 'get_sideBarList'
        }),
        watch: {
            // : 
            get_sideBarList: function () {
                let list = this.$store.state.viewState.sideBarList;
                let level = JSON.parse(sessionStorage.getItem("data")).level;
                let res = [];
                if (level > 5) {
                    res[0] = list[0]
                    res[1] = list[1];
                } else if (level > 1) {
                    res = [];

                    res[0] = list[2]
                    res[1] = list[3]
                }

                this.$data.dataSidebarList = res
                // 
                //
                if (level > 1 && level < 6) {

                    sessionStorage.setItem("router[0]", "");
                    sessionStorage.setItem("router[1]", "");
                    this.$data.breadcrumbLevelArr = this.$store.state.viewState.breadcrumbLevel;
                    this.$data.breadcrumbLevelArr[0].levelName = sessionStorage.getItem("router[0]");
                    this.$data.breadcrumbLevelArr[1].levelName = sessionStorage.getItem("router[1]");
                    this.$router.push({path: 'GetOrderSum'})
                }
            }
        }
    }
</script>

<style lang="sass" scoped>
    @import '../../../sass/main'

    #sidebar
        height: inherit
        +bC($C-theme-D)
        // main
        .el-menu
            height: 90%
            //  -  
            .material-icons
                +text-VA(middle)
            span
                @extend .material-icons
                +mL($F-info)
                +fS($F-info)
        // 
        .copyright
            +flexCenter
            +fS($F-text)
            color: rgba($F, .4)

</style>



